/* Global Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
  }
  
  /* Header Styles */
  .header {
    background-color: #333;
    color: #fff;
    padding: 20px 0;
    text-align: center;
    font-family: 'Arial', sans-serif;
  }
  
  .header h1 {
    margin-bottom: 20px;
  }
  
  /* Navigation Styles */
  .nav-buttons {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .nav-buttons .button {
    text-decoration: none;
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  .nav-buttons .button:hover {
    background-color: #45a049;
  }
  
  .nav-buttons .button:active {
    background-color: #3e8e41;
  }
  
  /* Form Styles */
  .form-container,
  .login-container {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 2rem auto;
    padding: 2rem;
  }
  
  .form-container h2,
  .login-container h2 {
    margin-bottom: 1rem;
  }
  
  .form-container label,
  .login-container label {
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .form-container textarea,
  .form-container select,
  .form-container button,
  .login-container input,
  .login-container button {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .form-container button,
  .login-container button {
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .form-container button:hover,
  .login-container button:hover {
    background-color: #0056b3;
  }
  
  /* Request List Styles */
  .request-list {
    max-width: 800px;
    margin: 2rem auto;
    padding: 1rem;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .request-list ul {
    list-style: none;
    padding: 0;
  }
  
  .request-list li {
    border-bottom: 1px solid #ddd;
    padding: 1rem 0;
  }
  
  .request-list li h2 {
    margin: 0;
  }
  
  .request-list li p {
    margin: 0.5rem 0;
  }
  
  .request-list a {
    color: #007bff;
    text-decoration: none;
  }
  
  .request-list a:hover {
    text-decoration: underline;
  }
  
  /* Request Details Styles */
  .request-details {
    max-width: 800px;
    margin: 2rem auto;
    padding: 1rem;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .request-details h2 {
    margin-bottom: 1rem;
  }
  